.sider-container{

  position: fixed;
  bottom: 0px;
  top: 0;
  right: 0;

  z-index: 2147483647;
  box-sizing: border-box;
  border-radius: 20px 0 0 20px;
  box-shadow: var(--gpts-sidebar-shadow);
  background-color: white;
  box-shadow: -12px 10px 5px rgba(78, 69, 69, 0.089);
  color: var(--gpts-primary-text-color);

  display: flex;
  flex-direction: column;

  width: 400px;
  position: fixed;
  top: 0px;
  bottom: 0px;
  right: 0px;
  transition: right 0.3s ease-in-out;
}

.sider{
  background-color: rgba(199, 174, 174, 0.432);
  position: absolute;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  gap: 6px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  width: 45px;
}

.hide-right{
  /* width: 0px!important; */
  right: -400px;
  /* width: 0px; */
}

.switch-button{
  position: absolute;
  width: 40px;
  border-top-left-radius: 18px;
  border-bottom-left-radius: 18px;
  background-color: white;
  box-shadow: -5px 5px 5px rgba(78, 69, 69, 0.089);
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  left: -40px;
  bottom: 5px;
  height: 40px;
}

.none{
  display: none!important;
}

.follow-button{
  position: absolute;
  width: 40px;
  border-top-left-radius: 18px;
  border-bottom-left-radius: 18px;
  background-color: white;
  box-shadow: -5px 5px 5px rgba(78, 69, 69, 0.089);
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  left: -40px;
  bottom: 50px;
  height: 40px;
}

.toggle{
  position: absolute;
  bottom: -3px;
  left: 10px;
}


/* Change the scrollbar track */
::-webkit-scrollbar {
  width: 6px;
}

/* Change the scrollbar thumb */
::-webkit-scrollbar-thumb {
  background-color: #d3c0c0;
  border-radius: 4px;
}

/* Change the scrollbar thumb on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #b69898;
}

.custom-select{
  height: 40px;
  border-color: #b69898;
  border-radius: 3px;
  width: 80px;
}